import React, {Component} from 'react'
import ReactSwipe from 'react-swipe';
import './style.less'

class Category extends Component{
	constructor(){
		super();
		this.state={
			index:0
		}
	}
	render(){
		const opt = {
			auto: 2500,
			callback: (index)=>{
				this.setState({
					index:index
				})
			}
		}
		return (
			<div id="home-category">
				<ReactSwipe swipeOptions={opt}>
					<div className="carousel-item">
						<ul className="clear-fix">
							<li className="float-left jingdian"></li>
							<li className="float-left ktv">KTV</li>
							<li className="float-left gouwu"></li>
							<li className="float-left shenghuofuwu"></li>
							<li className="float-left jianshenyundong"></li>
							<li className="float-left meifa"></li>
							<li className="float-left qinzi"></li>
							<li className="float-left xiaochikuaican"></li>
							<li className="float-left zizhucan"></li>
							<li className="float-left jiuba"></li>
						</ul>
					</div>
					<div className="carousel-item">
						<ul className="clear-fix">
							<li className="float-left meishi"></li>
							<li className="float-left dianying"></li>
							<li className="float-left jiudian"></li>
							<li className="float-left xuixianyule"></li>
							<li className="float-left waimai"></li>
							<li className="float-left huoguo"></li>
							<li className="float-left liren"></li>
							<li className="float-left dujiachuxing"></li>
							<li className="float-left zuliaoanmo"></li>
							<li className="float-left zhoubianyou"></li>
						</ul>
					</div>
					<div className="carousel-item">
						<ul className="clear-fix">
							<li className="float-left ribencai"></li>
							<li className="float-left SPA">SPA</li>
							<li className="float-left jiehun"></li>
							<li className="float-left xuexipeixun"></li>
							<li className="float-left xican"></li>
							<li className="float-left huochejipiao"></li>
							<li className="float-left shaokao"></li>
							<li className="float-left jiazhuang"></li>
							<li className="float-left chongwu"></li>
							<li className="float-left quanbufenlei"></li>
						</ul>
					</div>
				</ReactSwipe>
				<div className="index-container">
					<ul>
						<li className={this.state.index === 0 ? "selected" : ''}></li>
						<li className={this.state.index === 1 ? "selected" : ''}></li>
						<li className={this.state.index === 2 ? "selected" : ''}></li>
					</ul>
				</div>
			</div>
		)
	}
}

export default Category;


